export const routes = {
    home: '/',
    login: '/login',
    notFound: '/notFound',
    register: '/register',
}

const createRouteWithoutParam = (pathName) => (history) =>
    history.push({
        pathname: pathName,
    })

const createRouteWithoutParamReplace = (pathName) => (history) =>
    history.replace({
        pathname: pathName,
    })

export const routeTo = {
    login: createRouteWithoutParamReplace(routes.login),
    home: createRouteWithoutParam(routes.home),
    notFound: createRouteWithoutParam(routes.home),
    register: createRouteWithoutParam(routes.register),
}